<template>
	<view class="login-main-container">
		<view class="login-container">
			<view class="login-tabs">
				<view class="phone-login" @click="selectLoginPhone">
					<view class="font"> 验证码登录 </view>
					<view class="line" v-if="selectLogin == 0"></view>
				</view>
				<view class="psw-login" @click="selectLoginPsw">
					<view class="font"> 密码登录 </view>
					<view class="line" v-if="selectLogin == 1"></view>
				</view>
			</view>
			<view class="login-main" v-if="selectLogin == 0">
				<uni-forms ref="loginFormPhone" :modelValue="userData" :rules="loginPhoneRules">
					<uni-forms-item name="phoneNumber">
						<view>
							<text class="phone-num"> 邮箱 </text>
							<view class="phone-main">
								<image class="phone-img" src="../static/imgs/mine/login/phone.png" mode="aspectFill">
								</image>
								<view class="login-line"></view>
								<input type="text" class="phone-input" v-model="userData.phoneNumber"
									placeholder="请输入邮箱" />
							</view>
						</view>
					</uni-forms-item>
					<uni-forms-item name="code">
						<view>
							<text class="phone-num code-code"> 验证码 </text>
							<view class="phone-main code-main">
								<view class="phone-main-left">
									<image style="width: 27rpx; margin-right: 3rpx" class="phone-img"
										src="../static/imgs/mine/login/code.png" mode="aspectFill">
									</image>
									<view class="login-line"></view>
									<input type="text" class="phone-input" v-model="userData.code"
										placeholder="请输入验证码" />
								</view>
								<view class="phone-main-left">
									<view class="login-line"></view>
									<button type="text" size="mini" :plain="true" style="
                      border: none;
                      padding: 0;
                      height: 17px;
                      line-height: 17px;
                    " @click="getCode" class="phone-num" :disabled="disable" :class="{ codeGeting: isGeting }">
										{{ time }}
									</button>
								</view>
							</view>
						</view>
					</uni-forms-item>
				</uni-forms>
				<view class="login-button" @click="userLoginPhone">
					登录
				</view>
				<navigator url="../my/register" hover-class="none" class="login-bottom">
					<text type="default">立即注册</text>
				</navigator>
			</view>
			<view class="login-main" v-else>
				<uni-forms ref="loginFormPsw" :modelValue="userData" :rules="loginPswRules">
					<uni-forms-item name="phoneNumber">
						<view>
							<text class="phone-num"> 用户名 </text>
							<view class="phone-main">
								<image style="width: 28rpx; margin-right: 2rpx" class="phone-img"
									src="../static/imgs/mine/login/user.png" mode="aspectFill">
								</image>
								<view class="login-line"></view>
								<input type="text" class="phone-input" v-model="userData.phoneNumber"
									placeholder="请输入用户名" />
							</view>
						</view>
					</uni-forms-item>
					<uni-forms-item name="psw">
						<view>
							<text class="phone-num"> 密码 </text>
							<view class="phone-main">
								<image style="width: 22rpx; margin-right: 8rpx" class="phone-img"
									src="../static/imgs/mine/login/psw.png" mode="aspectFill">
								</image>
								<view class="login-line"></view>
								<input type="password" class="phone-input" v-model="userData.psw" placeholder="请输入密码" />
							</view>
						</view>
					</uni-forms-item>
				</uni-forms>
				<view class="login-button" @click="userLoginPsw">
					登录
				</view>
				<view class="register-psw">
					<navigator url="../my/register" hover-class="none" class="login-bottom">
						<text type="default">立即注册</text>
					</navigator>
					<navigator url="../my/password" hover-class="none" class="login-bottom">
						<text type="default" style="color: #000">忘记密码</text>
					</navigator>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				selectLogin: 0,
				userData: {
					phoneNumber: "",
					code: "",
					psw: "",
				},
				loginPhoneRules: {
					phoneNumber: {
						rules: [{
							required: true,
							errorMessage: "邮箱地址不能为空",
							trigger: "blur",
						}, {
							format: "email",
							errorMessage: "邮箱地址格式错误",
						}],
					},
					code: {
						rules: [{
							required: true,
							errorMessage: "请输入验证码",
							trigger: "blur",
						}, ],
					},
				},
				loginPswRules: {
					phoneNumber: {
						rules: [{
							required: true,
							errorMessage: "请输入用户名",
							trigger: "blur",
						}, ],
					},
					psw: {
						rules: [{
							required: true,
							errorMessage: "请输入密码",
						}, ],
					},
				},
				time: "获取验证码",
				count: 59,
				isGeting: false,
				disable: false,
				countDown: null,
			};
		},
		methods: {
			// 获取验证码
			getCode() {
				if (this.userData.phoneNumber == "") {
					uni.showToast({
						title: "请输入邮箱",
						icon: "none",
					});
				} else {
					uni.showLoading({
						title: "验证码获取中...",
					});
					uni.request({
						method: "GET",
						url: this.$baseUrl + "/newWatch/api/smsCodeGet",
						header: {
							"content-type": "application/json",
						},
						data: {
							phoneNumber: this.userData.phoneNumber,
							type: "2"
						},
						complete: (res) => {
							uni.hideLoading();
							if (res.statusCode == 200) {
								this.countDown = setInterval(() => {
									if (this.count < 1) {
										this.isGeting = false;
										this.disable = false;
										this.time = "获取验证码";
										this.count = 59;
										console.log(this.time);
										clearInterval(this.countDown);
										this.countDown = null;
									} else {
										this.isGeting = true;
										this.disable = true;
										this.time = this.count-- + "s后可重发";
									}
								}, 1000);
							}
							if (this.checkBack(res, true) == false) return;

							this.$isResolve();
						},
					});
				}
			},
			// 手机登录
			userLoginPhone() {
				this.$refs.loginFormPhone
					.validate()
					.then((res) => {
						console.log("表单数据", res);
						uni.showLoading({
							title: "登录中......",
						});
						uni.request({
							method: "POST",
							url: this.$baseUrl + "/newWatch/api/login",
							header: {
								"content-type": "application/json",
							},
							data: {
								username: this.userData.phoneNumber,
								code: this.userData.code,
							},
							complete: (res) => {
								console.log("登录啦", res.data);

								uni.hideLoading();

								if (this.checkBack(res, true) == false) {
									this.$isResolve();
									return;
								}

								this.setUserInfo(res);
								this.$isResolve();

								uni.switchTab({
									url: "/pages/mine",
								});
							},
						});
					})
					.catch((err) => {
						console.log("表单错误", err);
					});
			},
			// 密码登录
			userLoginPsw() {
				this.$refs.loginFormPsw
					.validate()
					.then((res) => {
						console.log("表单数据", res);
						uni.showLoading({
							title: "登录中......",
						});
						uni.request({
							method: "POST",
							url: this.$baseUrl + "/newWatch/api/login",
							header: {
								"content-type": "application/json",
							},
							data: {
								username: this.userData.phoneNumber,
								password: this.userData.psw,
							},
							complete: (res) => {
                console.log("登录啦", res.data);
								uni.hideLoading();
                
								if (this.checkBack(res, true) == false) {
									this.$isResolve();
									return;
								}
                if (res.data.code == 400) {
                  let msg = res.data.data;
                  uni.showToast({ title: msg, icon: 'none' });
                  return;
                } else {
                  this.setUserInfo(res);
                  uni.reLaunch({
                    url: "/pages/mine"
                  })
                }
							},
						});
					})
					.catch((err) => {
						console.log("表单错误", err);
					});
			},
			// 手机登录
			selectLoginPhone(e) {
				console.log(e);
				this.selectLogin = 0;
			},
			// 密码登录
			selectLoginPsw() {
				this.selectLogin = 1;
			},
		},
	};
</script>

<style lang="scss" scoped>
	.login-main-container {
		height: 100vh;
		background-color: #fbfbfb;

		.login-container {
			padding: 0 60rpx;
			padding-top: 150rpx;

			.login-tabs {
				display: flex;
				justify-content: space-between;

				.phone-login,
				.psw-login {
					width: 50%;
				}

				.font {
					text-align: center;
					color: #000;
					font-size: 28rpx;
					font-weight: bold;
				}
			}

			.line {
				width: 65rpx;
				height: 5rpx;
				margin: 0 auto;
				margin-top: 17rpx;
				background-color: #1ECC99;
				border-radius: 30rpx;
			}

			.login-main {
				margin-top: 70rpx;

				.phone-num {
					color: #000;
					font-size: 26rpx;
				}

				.codeGeting {
					color: #c3c3c3;
				}

				.code-code {
					margin-top: 40rpx;
				}

				.phone-main {
					// height: 40rpx;
					padding: 20rpx;
					margin-top: 26rpx;
					display: flex;
					align-items: center;
					background-color: #fff;
					border-radius: 20rpx;

					.phone-img {
						width: 30rpx;
						height: 30rpx;
					}

					.login-line {
						width: 2rpx;
						height: 40rpx;
						margin: 0 20rpx;
						background-color: #c3c3c3;
					}

					.phone-input {
						flex: 1;
						font-size: 26rpx;
						color: #c3c3c3;
					}
				}

				.code-main {
					justify-content: space-between;

					.phone-main-left {
						display: flex;
						align-items: center;
					}
				}

				.login-bottom {
					width: fit-content;
					margin-top: 10rpx;

					text {
						font-size: 24rpx;
						color: #1ECC99;
						cursor: pointer;
					}
				}

				.register-psw {
					display: flex;
					justify-content: space-between;
				}
			}

			.login-button {
				width: 630rpx;
				height: 80rpx;
				margin: 0 auto;
				margin-top: 70rpx;
				line-height: 80rpx;
				cursor: pointer;
				background-color: #1ECC99;
				color: #fff;
				font-size: 26rpx;
				border-radius: 60rpx;
				text-align: center;
			}
		}
	}
</style>
